export default{
	data(){
		return{
			userImg:'../../static/demo/datapic/4.jpg',
			imgIdx:4,
			isguanzhu:false
		}
	},
	methods:{
		changeImg(){
			this.imgIdx++
			if(this.imgIdx >9){
				this.imgIdx =4
			}
			this.userImg ='../../static/demo/datapic/'+this.imgIdx+'.jpg'
			// console.log(this.imgIdx)
		},
		changGuanzhu(){
			this.isguanzhu = !this.isguanzhu
			// console.log('guanzhu')
		}
	}
}
